.nav-block {
    background-color: #eeeeee;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    margin: 8px 0;
    padding: 16px;
}

.nav-block:hover, .nav-block.selected {
    background-color: #dddddd;
    color: #0366d6;
}

.nav-block-project-name {
    font-size: 20px;
    margin-bottom: 8px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
}

.nav-block:hover .nav-block-project-name {
    text-decoration: underline;
}

.nav-block-watchers {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.nav-block-star-svg {
    fill: currentColor;
}



