.contributor-holder {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    height: 84px;
    margin: 8px 0;
    width: 33%;
}

.contributor-avatar {
    background-repeat: no-repeat;
    background-size: contain;
    border-radius: 32px;
    height: 64px;
    margin: 0 16px;
    width: 64px;
}

.contributor-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: calc(100% - 96px);

}

.contributor-info .contributor-name {
    font-size: 20px;
    margin-bottom: 8px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
}

.contributor-contributions {
    font-size: 14px;
}

@media only screen and (max-width: 1100px) {
    .contributor-holder {
        width: 50%;
    }
}

@media only screen and (max-width: 850px) {
    .contributor-holder {
        height: 56px;
        width: 100%;
    }

    .contributor-avatar {
        height: 48px;
        width: 48px;
    }

    .contributor-info {
        width: calc(100% - 80px);
    }
}



